/*eslint-disable @typescript-eslint/no-unused-vars */

import React, { useState } from "react"
import { HLocalStorage } from "src/core/helpers"
import { Button, Card, CardBody, CardHeader, CardText } from "reactstrap"
import { NavLink } from "react-router-dom"
const Others = [
  { name: "Resize", link: "/dev/resize" },
  { name: "Notification", link: "/dev/notification" },
  { name: "Hook", link: "/dev/hook" },
  { name: "Custom Hook", link: "/dev/customhook" },
]
const PageDev = () => {
  return (
    <div>
      <Card>
        <CardHeader className="bg-light" onClick={() => {}}>
          <h5 className="mb-0">Other</h5>
        </CardHeader>
        <CardBody>
          <div className="list-group scrollarea">
            {Others &&
              Others.length > 0 &&
              Others.map((v, i) => {
                return (
                  <a
                    key={i}
                    href={v.link}
                    className="list-group-item list-group-item-action py-3 lh-tight"
                  >
                    <div className="d-flex w-100 align-items-center justify-content-between">
                      <strong>{v.name}</strong>
                    </div>
                    <div className="col-10 mb-1 small">{v.name}</div>
                  </a>
                )
              })}
          </div>
        </CardBody>
      </Card>
    </div>
  )
}

export default PageDev
